
import bus from '@/plugins/bus'
import { defineComponent, ref, computed } from 'vue'
import { useStore } from 'vuex'
import { useRouter } from 'vue-router'
import { useToast } from 'vue-toastification'
import Logo from '@/components/logo.vue'
import Button from '@/components/btn.vue'
export default defineComponent({
  components: {
    Button,
    Logo
  },
  props: {
    screenSize: {
      type: Number,
      required: true
    },
    scrollPos: {
      type: Number,
      required: true
    }
  },
  setup () {
    const store = useStore()
    const router = useRouter()
    const toast = useToast()
    const navbarStatus = ref(false)
    const userLog = computed(() => {
      return store.getters.getUser
    })
    const toCart = () => {
      const loginFirst = () => {
        toast.info('Please log in first to use cart')
        bus.emit('Login-open')
      }
      if (userLog.value._id) return router.push({ name: 'Cart' })
      return loginFirst()
    }
    const openLogin = () => {
      bus.emit('Login-open')
    }
    const openSignup = () => {
      bus.emit('Signup-open')
    }
    const openLogout = () => {
      bus.emit('Logout-open')
    }
    const toggleNav = () => {
      navbarStatus.value = !navbarStatus.value
    }
    return {
      navbarStatus,
      toggleNav,
      userLog,
      toCart,
      openLogin,
      openSignup,
      openLogout
    }
  }
})
